import React, { Component } from 'react';
import {Redirect} from 'react-router-dom';
import FckEditor from './FckEditor';
import FileUploadForm  from "./FileUploadForm";
import { database } from '../Components/FireBase';
import Select from 'react-select';
import AdminProduct from './AdminProduct';

const options = [
    { value: "SẢN PHẨM BÁN CHẠY" , label: "SẢN PHẨM BÁN CHẠY"},
    { value: "LAPTOP - PC" , label: "LAPTOP - PC" },
    { value: "PHỤ KIỆN ZIN", label: "PHỤ KIỆN ZIN" },
    { value: "APPLE WATCH", label: "APPLE WATCH" },
    { value: "NOKIA 8800", label: "NOKIA 8800" },
    { value: "APPLE IPHONE", label: "APPLE IPHONE" },

  ];

export class AdminEditProduct extends Component {
    
    componentWillMount() {
        if(this.props.itemProduct){
            this.setState({
                itemProduct:this.props.itemProduct,
            });
            for (const key in this.props.itemProduct) {
                if(this.props.itemProduct[key].cateProduct[1]==="SẢN PHẨM BÁN CHẠY"){
                    this.setState({
                        isChecked:true
                    });
                }
              
                this.setState({
                    selectedOption:this.props.itemProduct[key].cateProduct[0]
                });
                
            }
        }
        
    }
    
    constructor(props) {
        super(props);
        this.state={
            fckShort:'',
            fckLong:'',
            title:'',
            url:'',
            price:0,
            regular_price:0,
            cate:'',
            urlAlbum1:'',
            urlAlbum2:'',
            urlAlbum3:'',
            urlAlbum4:'',
            selectedOption: '',
            isUpdate:false,
            itemProduct:null,
            isChecked: false,
        }
    }
    toggleChange = () => {
        this.setState({
          isChecked: !this.state.isChecked,
        });
      }
    _tieudeSp = event => {
        const title= event.target.value;
        this.setState({title});
        console.log(`Tiêu đề sp:`, title);
    } 
    _giaSp = event => {
        let price = event.target.value;
        this.setState({price});
        console.log(`Tiêu đề sp:`, price);
    } 
    _giaChuaGiamSp = event => {
        let regular_price = event.target.value;
        this.setState({regular_price});
        console.log(`Tiêu đề sp:`, regular_price);
    } 
    _selectBox = selectedOption => {
        this.setState({ selectedOption });
        console.log(`Option selected:`, selectedOption);
    };
    _getContentShort(content){
        console.log('Content Bai Viet: '+content);
        this.setState({fckShort:content})
    }
    _getContentLong(content){
        console.log('Content Bai Viet: '+content);
        this.setState({fckLong:content})
    }
    _getUrlImg(url){
     //   console.log('_getUrlImg: '+ JSON.stringify(url.url));
        this.setState({url:url.url}); 
    }
    _getUrlImgAlbum1(url){
       // console.log('_getUrlImgAlbum: '+ JSON.stringify(url.url));
        this.setState({urlAlbum1:url.url}); 
        //console.log('this.state.urlAlbum: '+this.state.urlAlbum);
    }
    _getUrlImgAlbum2(url){
       // console.log('_getUrlImgAlbum: '+ JSON.stringify(url.url));
        this.setState({urlAlbum2:url.url}); 
        //console.log('this.state.urlAlbum: '+this.state.urlAlbum);
    }
    _getUrlImgAlbum3(url){
       // console.log('_getUrlImgAlbum: '+ JSON.stringify(url.url));
        this.setState({urlAlbum3:url.url}); 
        //console.log('this.state.urlAlbum: '+this.state.urlAlbum);
    }
    _getUrlImgAlbum4(url){
       // console.log('_getUrlImgAlbum: '+ JSON.stringify(url.url));
        this.setState({urlAlbum4:url.url}); 
        //console.log('this.state.urlAlbum: '+this.state.urlAlbum);
    }
    _showSpLon(sp){
        if(sp){
            return (
                <div style={{paddingTop:10}}>
                    <img style={{width:"40%"}} alt="anh lon" src={sp}/>
                </div>
                
            )
        }
    }
   
    componentDidUpdate(){
        // console.log('this.state.urlAlbum: '+this.state.urlAlbum1);
        // console.log('this.state.urlAlbum: '+this.state.urlAlbum2);
        // console.log('this.state.urlAlbum: '+this.state.urlAlbum3);
        // console.log('this.state.urlAlbum: '+this.state.urlAlbum4);
        // if(this.state.isUpdate===true){
        //     this.setState({isUpdate:false});
        //     alert("Cập nhật thành công");
            
        // }
         
       
    }
    getData(){
        var cateProduct=[];
        if(this.state.selectedOption.value===undefined){
            for (const key in this.state.itemProduct) {
                cateProduct[0]=this.state.itemProduct[key].cateProduct[0];
            }             
        }else{
            cateProduct[0]=this.state.selectedOption.value;
        }
        
        let dayNow = JSON.stringify(new Date());
       // console.log('getData: '+ JSON.stringify(this.state.itemProduct));
        
        // for (const key in this.state.itemProduct){
        //     var title = this.state.title;
        //     if(this.state.title===''){
        //         title = this.state.itemProduct[key].title;
        //     }
        // }
       var title =this.state.title;
       var short_description=this.state.fckShort;
       var content=this.state.fckLong; 
       
       
       
       if(this.state.isChecked===true){
         cateProduct[1]="SẢN PHẨM BÁN CHẠY";
       }else{
        cateProduct[1]="";
       }
   //    console.log('cateProduct[0]-1: '+cateProduct[0]);
       
       var img=this.state.url;
       var price=this.state.price;
       var regular_price=this.state.regular_price;
       var gallery_image= [];
       gallery_image[0] = this.state.urlAlbum1;
       gallery_image[1] = this.state.urlAlbum2;
       gallery_image[2] = this.state.urlAlbum3;
       gallery_image[3] = this.state.urlAlbum4;
    //    if(title==='' || short_description==='' || content==='' || status==='' || cateProduct[0]==='' 
    //    || img==='' || price==='' || regular_price==='' || gallery_image[0]==='' || gallery_image[1]==='' || gallery_image[2]==='' || gallery_image[3]===''){ 
        for (const key in this.state.itemProduct) {
            // if(this.state.selectedOption.value===''){
            //     cateProduct[0]=this.state.itemProduct[key].cateProduct[0];
            // }
          //  console.log('cate0: '+this.state.itemProduct[key].cateProduct[0]);
            
             var id = this.state.itemProduct[key].id;
             if(title==='')  title = this.state.itemProduct[key].title;
             if(short_description==='')  short_description=this.state.itemProduct[key].short_description;
             if(content==='')  content=this.state.itemProduct[key].content;
             //   if(cateProduct[1]==='')  cateProduct[1]=this.state.itemProduct[key].cateProduct[1];
             if(img==='')  img=this.state.itemProduct[key].img;
             if(price===0)  price=this.state.itemProduct[key].price;
             if(regular_price===0)  regular_price=this.state.itemProduct[key].regular_price;
             if(this.state.itemProduct[key].gallery_image){
                if(gallery_image[0]==='')  gallery_image[0]=this.state.itemProduct[key].gallery_image[0];   
                if(gallery_image[1]==='')  gallery_image[1]=this.state.itemProduct[key].gallery_image[1];   
                if(gallery_image[2]==='')  gallery_image[2]=this.state.itemProduct[key].gallery_image[2];   
                if(gallery_image[3]==='')  gallery_image[3]=this.state.itemProduct[key].gallery_image[3];   
             }       
        }

   //    console.log('short_description: '+ short_description);
       console.log('cateProduct[0]: '+ cateProduct[0]);
       console.log('cateProduct[1]: '+ cateProduct[1]);
    //   console.log('gallery_image[0]: '+ gallery_image[0]);
     //  console.log('id edit:'+id);
        
       let attrib=[];
       let author='';
       let average_rating=0;
       let post_modified_time=dayNow;
       let sku ='';
       let slug='';
       let the_date=dayNow;
       this.dataRef = database.ref('tbl_Sp/'+id);
       this.dataRef.set({
        "attrib":attrib,  
        "author":author,
        "average_rating":average_rating,
        "cateProduct":cateProduct,
        "content":content,
        "id":id,   
        "img":img,
        "post_modified_time":post_modified_time,
        "price":price,
        "regular_price":regular_price,
        "short_description":short_description,
        "sku":sku,
        "slug":slug,
        "status":'publish',
        "the_date":the_date,
        "title":title,
        "gallery_image":gallery_image,
       });
       alert("Cập nhật thành công sản phẩm");
       
       //cập nhật thay đổi mới
    //    let myInt = Number(new Date());
    //    this.dataRef = database.ref('upDate');
    //    this.dataRef.set(myInt);
       this.setState({isUpdate:true});
       //localStorage.setItem('upDate',myInt);
    }

    render() {

        if(this.state.itemProduct){
       //     console.log('cat0: '+this.state.selectedOption);
            
            for (const key in this.state.itemProduct) {
                var id = this.state.itemProduct[key].id;
                var title = this.state.itemProduct[key].title;
                var price = this.state.itemProduct[key].price;
                var regular_price = this.state.itemProduct[key].regular_price;
                var short_description = this.state.itemProduct[key].short_description;
                var img = this.state.itemProduct[key].img;
                var content = this.state.itemProduct[key].content;
                var gallery_image=[];
                if(this.state.itemProduct[key].gallery_image){
                gallery_image[0] = this.state.itemProduct[key].gallery_image[0];
                gallery_image[1] = this.state.itemProduct[key].gallery_image[1];
                gallery_image[2] = this.state.itemProduct[key].gallery_image[2];
                gallery_image[3] = this.state.itemProduct[key].gallery_image[3];
                }
               
                var cate = this.state.selectedOption;
                var cateProduct= [];
                for (const key in options) {
                   if(options[key].value===cate){
                    cateProduct.push(options[key]);
                   }
                }
                
            }
        //   console.log('gallery_image: '+JSON.stringify(cateProduct));
            
        }
        const { selectedOption } = this.state.selectedOption;
    //    const { selectedOption } = cateProduct;
       if(this.state.isUpdate===true) return <AdminProduct /> 
        return (
            <div>
                <div className="row">
                    <div className="col-md-4">
                    <Select
                    value={selectedOption}
                    onChange={this._selectBox}
                    options={options}
                   />
                   <label>
                        <input type="checkbox"
                        checked={this.state.isChecked}
                        onChange={this.toggleChange}
                        />
                        SẢN PHẨM BÁN CHẠY
                    </label>
                    </div>
                    <div className="col-md-4">
                    <input 
                    style={{width:"100%"}}
                    type="text" 
                    placeholder="Tên sản phẩm"
                    defaultValue={title}
                    // onChange={this.handleInput}
                    onBlur={this._tieudeSp} 
                    required />
                    </div>
                    <div className="col-md-2">
                    <input 
                    style={{width:"100%"}}
                    type="text" 
                    placeholder="Giá sản phẩm"
                    defaultValue={price}
                    // onChange={this.handleInput}
                    onBlur={this._giaSp} 
                    required />
                    </div>
                    <div className="col-md-2">
                    <input 
                    style={{width:"100%"}}
                    type="text" 
                    placeholder="Giá chưa giảm"
                    defaultValue={regular_price}
                    // onChange={this.handleInput}
                    onBlur={this._giaChuaGiamSp} 
                    required />
                    </div>
                </div>
                
                <div className="row">
                    <div className="col-md-6">
                    <h2>Mô tả ngắn sản phẩm</h2>
                    <FckEditor content={short_description} getContent={(content)=>this._getContentShort(content)}/>
                    </div>
                    <div className="col-md-6">
                    <h2>Mô tả Chi tiết sản phẩm</h2>
                    <FckEditor content={content}  getContent={(content)=>this._getContentLong(content)}/>
                    </div>
                </div>
                <p>Ảnh Sản phẩm</p>
                <div className="row">
                    <div className="col-md-6">
                    <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=anhsp'} tk={"Cập nhật Banner"} getUrlImg={(url)=>this._getUrlImg(url)} />
                    
                    </div>
                    <div className="col-md-6">
                        {this._showSpLon(img)}
                    </div>
                </div>
                <p>Album Ảnh Sản phẩm</p>
                <div className="row">
                    <div className="col-md-3">
                    <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=anhspmini1'} tk={"Cập nhật Banner"} getUrlImg={(url)=>this._getUrlImgAlbum1(url)} />
                    {this._showSpLon(gallery_image[0])}
                    </div>   
                    <div className="col-md-3">
                    <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=anhspmini2'} tk={"Cập nhật Banner"} getUrlImg={(url)=>this._getUrlImgAlbum2(url)} />
                    {this._showSpLon(gallery_image[1])}
                    </div>   
                    <div className="col-md-3">
                    <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=anhspmini3'} tk={"Cập nhật Banner"} getUrlImg={(url)=>this._getUrlImgAlbum3(url)} />
                    {this._showSpLon(gallery_image[2])}
                    </div>   
                    <div className="col-md-3">
                    <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=anhspmini4'} tk={"Cập nhật Banner"} getUrlImg={(url)=>this._getUrlImgAlbum4(url)} />
                    {this._showSpLon(gallery_image[3])}
                    </div>   
                    <hr/>
                    <button onClick={(id)=>this.getData(id)}>Cập nhật</button>
                </div>
            </div>
        );
    }
}

export default AdminEditProduct;
