import React, { Component } from 'react';
import { database } from '../Components/FireBase';
import Select from 'react-select';
import AdminEditProduct from './AdminEditProduct';
const options = [
    { value: "SẢN PHẨM BÁN CHẠY" , label: "SẢN PHẨM BÁN CHẠY"},
    { value: "LAPTOP - PC" , label: "LAPTOP - PC" },
    { value: "PHỤ KIỆN ZIN", label: "PHỤ KIỆN ZIN" },
    { value: "APPLE WATCH", label: "APPLE WATCH" },
    { value: "NOKIA 8800", label: "NOKIA 8800" },
    { value: "APPLE IPHONE", label: "APPLE IPHONE" },

  ];
export class AdminProduct extends Component {
    getProductByFirebase(){
        this.dataRef = database.ref('tbl_Sp'); 
        this.dataRef.on('value', (snapshot) => {
            let infoProduct = snapshot.val();
            var product =[];
            for (const key in infoProduct) {
                infoProduct[key].idSp= key;
                product.push(infoProduct[key]);
              
            }
            this.setState({product:product});
        })
    }
    componentWillMount() {
       this.getProductByFirebase();
      
    }
    constructor(props) {
        
        super(props);
        this.state={
            product:null,
            isShow:false,
            isDelete:false,
            isEdit:false,
            idEdit:'',
            selectedOption: options[0],
            productByCate:null,
            itemProduct:null,
        }
    }
    getCate(){
        var cate =[];
        for (const key in this.state.product) {
            cate.push(this.state.product[key].cateProduct[0]);
        }
     // let cateNew = Array.from(new Set(cate));

     // console.log(cateNew);
        
    }
    handleChange = selectedOption => {
        this.setState({ selectedOption });
       console.log(`Option selected:`, selectedOption.value);
        var dataNew=[];
        if(selectedOption.value==="SẢN PHẨM BÁN CHẠY"){
            dataNew =  this.state.product.filter(value => value.cateProduct[0] === selectedOption.value || value.cateProduct[1] === selectedOption.value);
        } else{
             dataNew =  this.state.product.filter(value => value.cateProduct[0] === selectedOption.value);
        }
        
      //  console.log("dataNew: "+ JSON.stringify(dataNew));
        this.setState({productByCate:dataNew});
        
    };
    getID(event,id){
        event.preventDefault();
        console.log(id);
     //   console.log('productByCate: '+this.state.productByCate);
        let product = this.state.productByCate;
        let itemProduct = product.filter(value => value.idSp===id);
     //   console.log('itemProduct: '+ JSON.stringify(itemProduct));
        
        this.setState({
            isEdit:true,
            idEdit:id,
            itemProduct:itemProduct,
        });
  //      var item = this.state.data.filter(value => value.id ===id);
  
      }
    getIDXoa(event,id){
        event.preventDefault();
        console.log(id);
       this.dataRef = database.ref('tbl_Sp'); 
       this.dataRef.child(id).remove();
       this.setState({
           isShow:false
       });
       //console.log('selectedOption Xoa: '+ JSON.stringify(this.state.selectedOption.value));
     //  this.handleChange=this.state.selectedOption.value;
      // this.getProductByFirebase();
       //this.setState({isShow:true});
      //   this.handleChange();
  //      var item = this.state.data.filter(value => value.id ===id);
  
      }
      componentDidUpdate(){
          // load lần đầu cho select box
        if(this.state.product && this.state.isShow===false){
           // console.log(this.state.product);
           // console.log(this.state.selectedOption);
            let selectedOption = this.state.selectedOption;
            var dataNew =  this.state.product.filter(value => value.cateProduct[0] === selectedOption.value || value.cateProduct[1] === selectedOption.value);
            this.setState({
                productByCate:dataNew,
                isShow:true,
            });
        }
       }
    getProductbyCate(){
        if(this.state.productByCate!==null){
          //  console.log("productByCate: "+ JSON.stringify(this.state.productByCate));
            var data = this.state.productByCate
            return data.map((value,key)=>{
                //console.log(value.idSp+" - "+value.title+" - "+value.short_description+" - "+value.img+" - "+value.price+" - "+value.status);
                return(
                <tr key={key}>
                    <td>{value.idSp}</td>
                    <td>{value.title.substr(0, 50)}</td>
                    <td>{value.price}</td>
                    <td><img alt="anh" src={value.img} style={{width:100,height:100}}/></td>
                    <td>{value.status}</td>
                    <td>
                       <a href="/" onClick={(event)=>this.getID(event,value.idSp)}> Sửa |  </a>
                       <a href="/" onClick={(event)=>this.getIDXoa(event,value.idSp)}> Xóa </a>     
                    </td>
                </tr>
                )
            })
    }
}
    render() {
        const  selectedOption  = this.state.selectedOption;
        if(this.state.isEdit===true){
            return <AdminEditProduct itemProduct={this.state.itemProduct}/>
        }
        if(this.state.product===null){
            return false;
        }
        else{
          //  console.log('Product: '+ JSON.stringify(this.state.product));
            this.getCate();
        }
        // if(this.state.isShow===false)
        return (
            <div>
                <Select
                    value={selectedOption}
                    onChange={this.handleChange}
                    options={options}
                    
                />  
                <hr/>
                <table className="table table-bordered">
                <thead>
                    <tr>
                    <th>idSP</th>
                    <th>Tên Sản Phẩm</th>
                    <th>Đơn Giá</th>
                    <th>Hình Ảnh</th>   
                    <th>Trạng thái</th>
                    <th>Sửa</th>
                    </tr>
                </thead>
                <tbody>
                    {this.getProductbyCate()}
                </tbody>
                </table>
               

            </div>
        );
    }
}

export default AdminProduct;
