import React, { Component } from 'react';
import { database } from '../Components/FireBase';
import AdminEdit from './AdminEdit';
export class AdminFooter extends Component {
    
    componentWillMount() {
        this.dataRef = database.ref('tbl_Baiviet'); 
        this.dataRef.on('value', (snapshot) => {
            let infoBaiviet = snapshot.val();
            var baiviet =[];
            for (const key in infoBaiviet) {
                infoBaiviet[key].id= key;
                baiviet.push(infoBaiviet[key]);
              
            }
            this.setState({baiviet:baiviet});
        })
    }
    
    constructor(props) {
        super(props);
        this.state={
            baiviet:null,
            isShow:false,
            idEdit:''
        }
    }
    getID(event,id){
        event.preventDefault();
        console.log(id);
        this.setState({
            isShow:true,
            idEdit:id
        });
  //      var item = this.state.data.filter(value => value.id ===id);
  
      }
    getBaiViet(){
        if(this.state.baiviet!==null){
            //console.log("bai viet: "+ JSON.stringify(this.state.baiviet['key']));
            var baiviet = this.state.baiviet;
            return baiviet.map((value,key)=>{
               // console.log("id: "+value.id);
               
                return(
                    <tr key={key}>
                    <td>{value.title}</td>
                    <td>{value.content.substr(0, 50)}</td>
                    <td>
                       <a href="/" onClick={(event)=>this.getID(event,value.id)}> Sửa </a>     
                    </td>
                    <td>{value.category}</td>
                    </tr>
                )
            })
        }
    }
    render() {
        if(this.state.isShow===false){
        return (
            <div>
                <table className="table table-bordered">
                <thead>
                    <tr>
                    <th>Tiêu đề</th>
                    <th>Nội dung</th>
                    <th>Sửa</th>
                    <th>Danh mục</th>
                    </tr>
                </thead>
                <tbody>
                    {this.getBaiViet()}
                    
                </tbody>
                </table>

            </div>
        )
        } 
        if(this.state.isShow===true){
            return(
                <AdminEdit idEdit={this.state.idEdit} />
            )
        }
    }
}

export default AdminFooter;
