import React, { Component } from 'react';
import {connect} from 'react-redux';
import ReactHtmlParser from 'react-html-parser';
import formatMoney from './Function';
import FormMail from './FromMail';
const config = {
    api: 'https://azshopweb.com/khachhang/api/contact/',
    title: 'ĐIỀN THÔNG TIN KHÁCH HÀNG',
    successMessage: 'Cám ơn bạn ! Chúng tôi sẽ liên hệ lại bạn sớm nhất.',
    errorMessage: 'Đã có lỗi xãy ra!',
    fields:{
      firstName: '',
      lastName: '',
      email: '',
      sdt:'',
      diachi:'',
      msg: ''
    },
    fieldsConfig:  [
     { id: 1, label: 'HỌ VÀ TÊN', fieldName: 'firstName', type: 'text',placeholder:'HỌ VÀ TÊN', isRequired: true , klassName:'first-name-field'},     
     { id: 2, label: 'ĐỊA CHỈ EMAIL*', fieldName: 'email', type: 'email', placeholder: ' ĐỊA CHỈ EMAIL', isRequired: true , klassName:'email-field'},
     { id: 3, label: 'SỐ ĐIỆN THOẠI*', fieldName: 'sdt', type: 'text', placeholder: 'SỐ ĐIỆN THOẠI', isRequired: true , klassName:'last-name-field'},
     { id: 4, label: 'ĐỊA CHỈ*', fieldName: 'diachi', type: 'text', placeholder: ' ĐỊA CHỈ', isRequired: true , klassName:'email-field'},
     { id: 5, label: 'GHI CHÚ', fieldName: 'msg', type: 'textarea',placeholder:'GHI CHÚ.....', isRequired: true , klassName:'message-field'}
    ]
  }

export class Contact extends Component {
    constructor(props) {
        super(props);
        this.state={
            idProduct:[],
            isContact:false,
            isDetails:false

        }
    }
    getQueryVariable(variable)
  {
          var query = window.location.search.substring(1);
        //  console.log(query)//"app=article&act=news_content&aid=160990"
          var vars = query.split("?");   
         // console.log(vars) //[ 'app=article', 'act=news_content', 'aid=160990' ]
          for (var i=0;i<vars.length;i++) {
            var pair = vars[i].split("=");
         //   console.log(pair)//[ 'app', 'article' ][ 'act', 'news_content' ][ 'aid', '160990' ] 
          if(pair[0] === variable){
            //  console.log('id: '+ pair[1]);
             this.setState({idProduct:pair[1]});
             return pair[1];
          }
           }
           return(false);
  }  
    componentWillMount() {
        console.log('contact');
        var idSp= this.getQueryVariable('id');
        console.log('idSp: '+idSp);
        if(idSp){
            sessionStorage.setItem("idsp"+idSp,idSp);
            this.props.getIsContact(idSp);
        }
        
        
    }
    componentDidUpdate(){
    //    console.log('this.props.itemProduct: '+this.props.idProduct);
        if(this.props.idProduct && this.state.isContact===false){
        if ( typeof(Storage) !== "undefined") {
      //     console.log('sessionStorage.length: '+sessionStorage.length);
      //     var dataSession = JSON.parse(sessionStorage);
           var idProduct =[];
           for (const key in sessionStorage) {
               //console.log('key: '+key.substr(0,4));
                if(key.substr(0,4)==="idsp"){
                  //  console.log('key: '+sessionStorage[key]);
                    idProduct.push(sessionStorage[key]);
                }
               }
           // console.log('id product: '+idProduct);
               this.setState({
                   idProduct:idProduct,
                   isContact:true,
                });
           }
        }
    }
    getID(event,id){
        event.preventDefault();
          console.log(id);
    }
    render() {
        if(this.props.isContact===false ) return false;
        if(this.state.idProduct){
            //console.log('id product: '+this.state.idProduct);
            var sp = JSON.parse(localStorage.getItem('sp'));
            
                var item='';
                for (let index = 0; index < this.state.idProduct.length; index++) {
                    var element = parseInt(this.state.idProduct[index]);
                                       
                    for (const key in sp) {
                        if(element===sp[key].id){
                            console.log('sp title: '+sp[key].title);
                            console.log('sp price: '+sp[key].price);
                            item=item+"<tr><td>"+sp[key].title+"</td><td>1</td><td>"+formatMoney(sp[key].price,0,".")+"</td><td>"+formatMoney(sp[key].price,0,".")+"</td></tr>";
                        }    
                    
                    }
                    
                 }
        }
        return (
            <div className="container">
                <div className="row donhang">
                    <div className="col">
                    <p>SẢN PHẨM QUÝ KHÁCH ĐÃ CHỌN</p>
                    <table className="table table-striped w-50" style={{margin:'auto'}}>
                    <thead>
                    <tr>
                        <th>Tên Sản phẩm</th>
                        <th>Số lượng</th>
                        <th>Đơn giá</th>
                        <th>Thành tiền</th>
                    
                    </tr>
                    </thead>
                    <tbody>
                    {ReactHtmlParser(item)}                    
                    </tbody>
                </table>
                    </div>
                </div>
                <div className="row">
                    <div className="col">
                    <FormMail config={config} getItem={item}/>
                    </div>
                </div>
                
            </div>
        );
    }
}
const mapStateToProps = (state, ownProps) => {
    return {
        isContact: state.isContact,
        itemProduct:state.itemProduct,
        idProduct:state.idProduct,
    }
  }
  // this.props.testStore
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        getIsContact: (item) => {
            dispatch({type:"IS_CONTACT",item})
          }
    }
  }
export default connect(mapStateToProps, mapDispatchToProps)(Contact);  

