import React, { Component } from 'react'; 
import FckEditor from './FckEditor';
import FileUploadForm  from "./FileUploadForm";
import { database } from '../Components/FireBase';
import Select from 'react-select';
import AdminFooter from './AdminFooter';
// import Admin from './Admin';
const options = [
    { value: 'footer', label: 'Footer' },
    { value: 'baiviet', label: 'Bài Viết' },
    { value: 'spbc', label: 'Sản Phẩm Bán Chạy' },
    { value: 'appleiphone', label: 'APPLE IPHONE' },
    { value: 'applewatch', label: 'APPLE WATCH' },
    { value: 'nokia8800', label: 'NOKIA 8800' },
    { value: 'phukienzin', label: 'PHỤ KIỆN ZIN' },
    { value: 'laptoppc', label: 'LAPTOP - PC' },
  ];
export class AdminEdit extends Component {
    
    componentWillMount() {
        if(this.props.idEdit){
            this.dataRef = database.ref('tbl_Baiviet/'+this.props.idEdit);
            this.dataRef.on('value', (snapshot) => {
                let infoBaiviet = snapshot.val();
                console.log("ID bai viet: "+ JSON.stringify(infoBaiviet));
                this.setState({
                    fck1:infoBaiviet.content,
                    selectedOption:infoBaiviet.category,
                    value:infoBaiviet.title,
                    url:infoBaiviet.img,
                });
            })
           }
    }
    
    constructor(props) {
        super(props);
        this.state={
            fck1:'',
            value:'',
            url:'',
            selectedOption: null,
            isUpdate:false,
            isImg:false,
        }
    }

    handleInput = event => {
        // const { value } = event.target;
        // this.setState({value});
     }

    handleInputValidation = event => {
        const { value } = event.target;
        this.setState({value});
    } 

    handleInputSelect = event => {
        const { cate } = event.target;
        this.setState({cate});
    }

    componentDidMount(){
       // console.log('componentDidMount');
      
           
    }
    componentDidUpdate(){
       // console.log('componentDidUpdate bai viet');
        
    }
    getContent(content){
        console.log('Content Bai Viet: '+content);
        this.setState({fck1:content})
    }
    // componentDidUpdate(){
    //     if(this.state.fck1!==''){
    //         console.log('Noi dung: '+this.state.fck1);
    //     }
    //     if(this.state.value!==''){  
    //        console.log('Tieu de: '+ this.state.value);
    //     }
    //     if(this.state.selectedOption!==''){
    //        //console.log('Cate: '+ JSON.stringify(this.state.selectedOption.value));
    //     }
        
    // }
    getData(){
              
        //var cateProduct='';
       // console.log('selectedOption1: '+this.state.selectedOption);
        if(this.state.fck1!=='' && this.state.value!==''){
            // console.log('value: '+ this.state.value);
            // console.log('getContent: '+this.state.fck1);
            // console.log('Url: '+this.state.url);
            // console.log('selectedOption: '+this.state.selectedOption);
            let tieude = this.state.value;
            let noidung = this.state.fck1;
            let url = this.state.url;
            var selectedOption = this.state.selectedOption;
            if(typeof(selectedOption)!=="string"){
                selectedOption = this.state.selectedOption.value;
            }
            console.log('tieude: '+tieude);
            console.log('selectedOption: '+selectedOption);
            console.log('getContent: '+noidung);
            console.log('Url: '+url);
            let date = new Date();
            let dateNow = JSON.stringify(date);
            let strDateNow = dateNow.substr(1,10);
            let strTime = date.getHours()+":"+date.getMinutes()+":"+date.getSeconds();
            this.dataRef = database.ref('tbl_Baiviet/'+this.props.idEdit);    
                try {
                    this.dataRef.set({
                        "title":tieude,
                        "content":noidung,
                        "date":strDateNow,
                        "img":url,
                        "time":strTime,
                        "status":1,
                        "category":selectedOption,
                    }); 
                } catch (error) {
                    
                }   
            }
          //  console.log("Cập nhật thành công: "+this.props.idEdit);
            

            //cập nhật thay đổi mới
            let myInt = Number(new Date());
            this.dataRef = database.ref('upDate');
            this.dataRef.set(myInt);
            alert("Cập nhật thành công");
           this.setState({isUpdate:true});
          //  localStorage.setItem('upDate',myInt);
    }
    getData1(){
        this.setState({isUpdate:true});
    }
    getUrlImg(url){
       // console.log('url image: '+ JSON.stringify(url.url));
        this.setState({url:url.url,isImg:true}); 
    }
    getShow(sp){
        console.log('sp:'+sp);
        if(sp && this.state.isImg===true ){
            var myInt = Number(new Date());
            var imgSrc = sp+"?"+myInt;
                
            
            return (
                <div style={{paddingTop:10}}>
                    <p>Banner cập nhật mới</p> 
                    <img style={{width:"10%"    }} alt="anh lon" src={imgSrc}/>
                </div>
                
            )
        }
     }
    handleChange = selectedOption => {
        this.setState({ selectedOption });
       // console.log(`Option selected:`, selectedOption);
    };
    render() {
        if(this.state.isUpdate===true) return ( <AdminFooter /> )
     //   if(this.state.isUpdate===true) return ( <Admin /> )
        const { selectedOption } = this.state;
        return (
            <div>
                <input 
                    style={{width:"100%"}}
                    type="text" 
                    defaultValue={this.state.value}
                    placeholder="Tiêu đề bài viết"
                    // onChange={this.handleInput}
                    onBlur={this.handleInputValidation} 
                    required />
                <p style={{display:'none'}}>
                <Select
                    value={selectedOption}
                    onChange={this.handleChange}
                    options={options}
                    
                />
                </p>
                <FckEditor 
                   content={this.state.fck1}
                   getContent={(content)=>this.getContent(content)} 
                   
                />
                <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=baiviet'} tk={"Cập nhật Banner"} getUrlImg={(url)=>this.getUrlImg(url)} />
                <hr />               
                {this.getShow(this.state.url)}
                {/* <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=test'} tk={"Cập nhật Banner"} /> */}
                <button onClick={()=>this.getData()}>Cập nhật</button>
                <button onClick={()=>this.getData1()}>Không lưu</button>
            </div>
        );
    }
}

export default AdminEdit;
