import React, { Component } from 'react';
import FileUploadForm  from "./FileUploadForm";
export class AdminBannerChay extends Component {
    constructor(props) {
        super(props);
        this.state={
            isUpdate:'',
            url:'',
            url1:'',
            url2:'',
            url3:'',
            url4:'',
        } 
    }
    getUrlImg1(url){
        // console.log('url image: '+ JSON.stringify(url.url));
         this.setState({url1:url.url});
        // alert("Cập nhật thành công");
     }
    getUrlImg2(url){
        // console.log('url image: '+ JSON.stringify(url.url));
         this.setState({url2:url.url});
        // alert("Cập nhật thành công");
     }
    getUrlImg3(url){
        // console.log('url image: '+ JSON.stringify(url.url));
         this.setState({url3:url.url});
        // alert("Cập nhật thành công");
     }
    getUrlImg4(url){
        // console.log('url image: '+ JSON.stringify(url.url));
         this.setState({url4:url.url});
        // alert("Cập nhật thành công");
     }
     getShowCurrent1(){
        // if(this.state.url1===''){
            
             return(
                <img alt="anh" style={{width:'50%'}} src="https://azshopweb.com/khachhang/upload/uploads/banner1.png"/>
             )
        // }
     }
     getShowCurrent2(){
         if(this.state.url2===''){
             return(
                <img alt="anh" style={{width:'50%'}} src="https://azshopweb.com/khachhang/upload/uploads/banner2.png"/>
             )
         }
     }
     getShowCurrent3(){
         if(this.state.url3===''){
             return(
                <img alt="anh" style={{width:'50%'}} src="https://azshopweb.com/khachhang/upload/uploads/banner3.png"/>
             )
         }
     }
     getShowCurrent4(){
         if(this.state.url4===''){
             return(
                <img alt="anh" style={{width:'50%'}} src="https://azshopweb.com/khachhang/upload/uploads/banner4.png"/>
             )
         }
     }
    
     getShow(sp){
        if(sp){
            var myInt = Number(new Date());
            var imgSrc = sp+"?"+myInt;
            console.log('sp:'+sp);
            
            return (
                <div style={{paddingTop:10}}>
                    <p>Banner cập nhật mới</p> 
                    <img style={{width:"50%"}} alt="anh lon" src={imgSrc}/>
                </div>
                
            )
        }
     }
    render() {
        return (
            <div>
                <h2>Cập nhật Banner Sản Phẩm</h2>
                <div className="row">
                <div className="col-3">
                <p>Banner 1</p>    
                <hr/>
                <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=banner1'} tk={"Cập nhật Banner 1"} getUrlImg={(url)=>this.getUrlImg1(url)} />
                <hr />
                {/* {this.getShowCurrent1()} */}
                {this.getShow(this.state.url1)}
                
                </div>
                <div className="col-3">
                <p>Banner 2</p> 
                <hr />
                <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=banner2'} tk={"Cập nhật Banner 2"} getUrlImg={(url)=>this.getUrlImg2(url)}  />
                <hr />
                {/* {this.getShowCurrent2()} */}
                {this.getShow(this.state.url2)}
                </div>
                <div className="col-3">
                <p>Banner 3</p>     
                <hr />
                <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=banner3'} tk={"Cập nhật Banner 3"} getUrlImg={(url)=>this.getUrlImg3(url)}  />
                <hr />
                {/* {this.getShowCurrent3()} */}
                {this.getShow(this.state.url3)}
                </div>
                <div className="col-3">
                <p>Banner 4</p> 
                <hr />
                <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=banner4'} tk={"Cập nhật Banner 4"} getUrlImg={(url)=>this.getUrlImg4(url)}  />
                <hr />
                {/* {this.getShowCurrent4()} */}
                {this.getShow(this.state.url4)}
                </div>
                
                
                </div>
            </div>
        );
    }
}

export default AdminBannerChay;
