import React, { Component } from 'react';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';

export class HomeMainCateLeft extends Component {
  componentWillMount() {
    if(localStorage.getItem('sp')){
       this.setState({data:localStorage.getItem('sp')});
    }
  }
  constructor(props) {
    super(props);
    this.state ={
       data:null

   }
}
    printCate(cate){
      var cate=["SẢN PHẨM BÁN CHẠY","APPLE IPHONE","APPLE WATCH","NOKIA 8800","LAPTOP - PC","PHỤ KIỆN ZIN"];
      return cate.map((value,key)=>{
        if(value!=="SLIDER"){
        return (
          <li key={key}> 
          {/* <a style={{color:'#610703',fontWeight:500}} href="/" onClick={(event)=>this.cateSp(event,key)}>{value}</a> */}
          <Link to={{ pathname: "/productDetail", search: "?id="+key }} style={{color:'#610703',fontWeight:500}}  onClick={(event)=>this.cateSp(event,key)}>{value}</Link>
          </li>
        );
        }
      })
      
    } 
    cateSp(event,key){
      event.preventDefault();
      console.log('key: '+key);
      if(this.state) {
        var data = JSON.parse(this.state.data);
        var dataNew=[];
        if(key===0) {
          for (const key in data) {
            if(data[key].cateProduct[0]==="SẢN PHẨM BÁN CHẠY" || data[key].cateProduct[1]==="SẢN PHẨM BÁN CHẠY"){
              dataNew.push(data[key]);
            }
          }
          dataNew[0].cateProduct[0]="SẢN PHẨM BÁN CHẠY";
          this.props.productSearch(dataNew.reverse());
        }
        if(key===1) {
          for (const key in data) {
            if(data[key].cateProduct[0]==="APPLE IPHONE"){
              dataNew.push(data[key]);
            }
          }
          this.props.productSearch(dataNew.reverse());
        }
        if(key===2) {
          for (const key in data) {
            if(data[key].cateProduct[0]==="APPLE WATCH"){
              dataNew.push(data[key]);
            }
          }
          this.props.productSearch(dataNew.reverse());
        }
        if(key===3) {
          for (const key in data) {
            if(data[key].cateProduct[0]==="NOKIA 8800"){
              dataNew.push(data[key]);
            }
          }
          this.props.productSearch(dataNew.reverse());
        }
        if(key===4) {
          for (const key in data) {
            if(data[key].cateProduct[0]==="LAPTOP - PC"){
              dataNew.push(data[key]);
            }
          }
          this.props.productSearch(dataNew.reverse());
        }
        if(key===5) {
          for (const key in data) {
            if(data[key].cateProduct[0]==="PHỤ KIỆN ZIN"){
              dataNew.push(data[key]);
            }
          }
          this.props.productSearch(dataNew.reverse());
        }
   
      }
      
    }
    render() {
         return (
          <aside className="col-md-3">
          <h5 style={{color:'#610703',fontWeight:500}} className="text-uppercase">DANH MỤC SẢN PHẨM</h5>
          <ul className="menu-category">
            {this.printCate(this.props.cate)}
           </ul>
        </aside> 
        );
    }
}

const mapStateToProps = (state, ownProps) => {
  return {
    cate:state.cate,
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    productSearch: (item) => {
      dispatch({type:"PRODUCT_SEARCH",item})
    }
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(HomeMainCateLeft);

