import React, { Component } from 'react';
import {connect} from 'react-redux';
import {Route} from 'react-router-dom';
import Header from './Header';
import Home from './Home';
import ProductSearch from './ProductSearch';

import Contact from './Contact';
import { ProductDetails } from './ProductDetails';
export class FrontEnd extends Component {
    getIsHome(){
      if(this.state.isHome===true){
        return <Home />
      }
    }
    getIsContact(){
      if(this.state.isContact===true){
        return <Contact />
      }
    }
    getIsProductDetails(){
      if(this.state.isProductDetails===true){
        return <ProductDetails />
      }
    }
    getIsProductSearch(){
      if(this.state.isProductSearch===true){
        return <ProductSearch />
      }
    }
    componentWillMount() {
       // console.log('FrontEnd: '+this.props.isShow);
         // console.log('url: '+ window.location.protocol);
          var domain = window.location.protocol+"//"+ window.location.host+"/";
          console.log('url domain: '+ domain);
          if(domain===window.location.href){
            console.log('url = nhau');
             this.setState({isHome:true})
          }
          if(this.props.isShow===true){
            this.setState({
              isShow:true
            })
          }
          if(domain.indexOf("contact")!==-1){
            this.setState({
              isContact:true
            });
          }
          if(domain.indexOf("productDetail")!==-1){
            this.setState({
              isProductDetails:true,
              isProductSearch:true,
            });
          }
      }
      
        constructor(props) {
        super(props);
        this.state={
            isShow:false,
            isHome:false,
            isContact:false,
            isProductDetails:false,
            isProductSearch:false,
        }
      }
    render() {
      //  if(this.state.isShow === true) return false;
        return (
            <div>
                <Header/>
                
                {this.getIsHome()}
                <ProductSearch/>
                {this.getIsContact()}
                {this.getIsProductSearch()}
                
            </div>
        );
    }
}


const mapStateToProps = (state, ownProps) => {
    return {
      isShow:state.isShow,
    }
  }
  // this.props.testStore
  const mapDispatchToProps = (dispatch, ownProps) => {
    return {
  
    }
  }
  
  export default connect(mapStateToProps, mapDispatchToProps)(FrontEnd);