import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';
import formatMoney from './Function';
import ReactHtmlParser from 'react-html-parser';



export class ProductDetails extends Component {
  constructor(props) {
    super(props);
    this.state ={
      idProduct:'',
      item:null,
      imgLon:'',
    }
  }
  
 getQueryVariable(variable)
  {
          var query = window.location.search.substring(1);
        //  console.log(query)//"app=article&act=news_content&aid=160990"
          var vars = query.split("?");   
         // console.log(vars) //[ 'app=article', 'act=news_content', 'aid=160990' ]
          for (var i=0;i<vars.length;i++) {
            var pair = vars[i].split("=");
         //   console.log(pair)//[ 'app', 'article' ][ 'act', 'news_content' ][ 'aid', '160990' ] 
          if(pair[0] === variable){
            //  console.log('id: '+ pair[1]);
             this.setState({idProduct:pair[1]});
             return pair[1];
          }
           }
           return(false); 
  }  
componentWillMount() {
  
  var idSp= this.getQueryVariable('id');
  var sp = JSON.parse(localStorage.getItem('sp'));
       var itemProduct=[];
       for (const key in sp) {

          if(sp[key].id=== parseInt(idSp)){
            
              itemProduct.push(sp[key]);
          }
  } 
  // console.log('itemProduct: '+ JSON.stringify(itemProduct));
  if(itemProduct) {
     this.setState({item:itemProduct});
     console.log('itemProduct: '+ JSON.stringify(itemProduct));
     
  }
   
}
componentDidMount() {
  var ps = document.getElementById("ProductSearch");
  console.log('search: '+ps);
  if(ps){
    ps.remove();
  }
  
}
getImage(imgData){
  console.log("imgData: "+imgData);
    if(imgData){
    return imgData.map((value,key)=>{
       return (
          <div key={key} className="item-gallery"> 
            <a href="/" onClick={(event)=>this.getID(event,value)}>
              <img alt="anh"  src={value} />
            </a>
            {/* <div className="modal" id={myModel}>
              <div className="modal-body">
                <img alt="anh"  src={value} />
              </div>
            </div>  */}
          </div>
       )
    })
  }
}
getID(event,id){
  event.preventDefault();
  console.log(id);
  this.setState({
    imgLon:id
  })
}
getDatMua(event,id){
  event.preventDefault();
  console.log(id);
  sessionStorage.setItem("idsp"+id,JSON.stringify(id));
  this.props.getIsContact(id);
}
getContent(content){
  //value.content
  if(content){
  return (
    <article className="card mt-3">
                <div className="card-body">
                  <h4>Detail overview</h4>
                  <p>{ReactHtmlParser(content)}</p>
                </div> 
    </article> 
  )
  }
}
showItem(){
var items = this.state.item;
console.log("items: "+ JSON.stringify(items));
    return items.map((value,key)=>{
          return (
        <section key={key} className="section-content bg padding-y-sm">
        <div className="container">
        <a href="/home"><h5>Về Trang chủ</h5></a>
          <div className="row">
            <div className="col-xl-12 col-md-12 col-sm-12">
              <main className="card">
                <div className="row no-gutters">
                  <aside className="col-sm-6 border-right">
                    <article className="gallery-wrap"> 
                      <div className="img-big-wrap">
                        <div> <a href="/" onClick={(event)=>this.getID(event,value.img)}><img alt="anh"  src={value.img} /></a></div>
                      </div> 
                      <div className="img-small-wrap">
                      {this.getImage(value.gallery_image)}
                       
                      </div> 
                    </article> 
                  </aside>
                  <aside className="col-sm-6">
                    <article className="card-body">
                      {/* short-info-wrap */}
                      <h3 className="title mb-3">{value.title}</h3>
                      <div className="mb-3"> 
                        <var className="price h3 text-warning"> 
                          <span className="currency" style={{color:'red'}}>VNĐ </span><span style={{color:'red'}} className="num">{formatMoney(value.price,0)}</span>
                        </var> 
                     
                      </div> 
                      <dl>
                        <dt>Description</dt>
                        <dd>{ReactHtmlParser(value.short_description)}</dd>
                      </dl>
                      <dl className="row">
                        <dt className="col-sm-3">Model#</dt>
                        <dd className="col-sm-9">{value.sku}</dd>
                      </dl>
                      <dl className="row">
                      <dt className="col-sm-3"><p className="word-limit text-center">
                        {/* <button onClick={(event)=>this.getDatMua(event,value.id)} type="button" class="btn btn-danger">Đặt mua</button></p></dt> */}
                        <Link to={{ pathname: "/contact", search: "?id="+value.id }} class="btn btn-danger">Đặt mua</Link>
                        </p></dt>
                      </dl>
                      
                      <div className="rating-wrap">

                     
                      </div> 
              
      
                    </article> 
                  </aside> 
                </div> 
              </main> 
              
              {this.getContent(value.content)}
            
            </div> 
         
          </div> 
        </div>
      </section>
      
          )

     });
}
  render() {
   // if(this.props.isProductDetails===false) return false;
    return (
      <div>
          {this.showItem()}        
      </div>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    isContact: state.isContact,
    isSearch: state.isSearch,
    isProductDetails:state.isProductDetails,
  }
}
// this.props.getItemProduct
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    getIsContact: (item) => {
      dispatch({type:"IS_CONTACT",item})
    },
    getDetails: () => {
      dispatch({type:"IS_DETAILS"})
    },
  }
}
// this.props.addData()

export default connect(mapStateToProps,mapDispatchToProps)(ProductDetails);
