import React, { Component } from 'react';
import { database } from '../Components/FireBase';

export class AdminMatKhau extends Component {
    constructor(props) {
        super(props);
        this.state={
            mkc:'',
            mk1:'',
            mk2:'',
            isSuccess:false,
        }
    }
    
    // matkhaucu = event => {
    //     this.setState({mkc:event.target.value});
    // }
    matkhau1 = event => {
        this.setState({mk1:event.target.value});
    }
    matkhau2 = event => {
        this.setState({mk2:event.target.value});
    }
    componentDidUpdate(){
     
      
    }
    getMatkhau(){
        if(this.state.mk1!=='' && this.state.mk2!=='' && this.state.isSuccess===false ){
            if(this.state.mk1===this.state.mk2 ){
                alert("đổi mật khẩu thành công");
                this.dataRef = database.ref('tbl_user/'+this.props.getKey);
                this.dataRef.set({
                    email:this.props.getUser.email,
                    isStatus:this.props.getUser.isStatus,
                    name:this.props.getUser.name,
                    password:this.state.mk1,
                })  
                this.setState({isSuccess:true});
            }
            else{
               alert("mật khẩu không trùng khớp");
           }
       }else{
           alert("mật khẩu không được bỏ trống");
       }
    }
    render() {
        console.log('User MK: '+ JSON.stringify(this.props.getUser));
        
        if(this.state){
            // console.log('mkc: '+this.state.mkc);
            // console.log('mkc: '+this.state.mk1);
            // console.log('mkc: '+this.state.mk2);

        }
        return (
            <div>
                {/* <input 
                    style={{width:"100%"}}
                    type="text" 
                    placeholder="Mật khẩu củ"
                    onBlur={this.matkhaucu} 
                    required /> */}
                <input 
                    style={{width:"100%"}}
                    type="text" 
                    placeholder="Mật khẩu mới"
                    onBlur={this.matkhau1} 
                    required />
                <input 
                    style={{width:"100%"}}
                    type="text" 
                    placeholder="Nhập lại Mật khẩu mới"
                    onBlur={this.matkhau2} 
                    required />
                <button onClick={()=>this.getMatkhau()}>Cập nhật</button>
            </div>
        );
    }
}

export default AdminMatKhau;

