import React, { Component } from 'react';
import FckEditor from './FckEditor';
import FileUploadForm  from "./FileUploadForm";
import { database } from '../Components/FireBase';
import Select from 'react-select';
// import AdminProduct from './AdminProduct';
const options = [
    { value: "SẢN PHẨM BÁN CHẠY" , label: "SẢN PHẨM BÁN CHẠY"},
    { value: "LAPTOP - PC" , label: "LAPTOP - PC" },
    { value: "PHỤ KIỆN ZIN", label: "PHỤ KIỆN ZIN" },
    { value: "APPLE WATCH", label: "APPLE WATCH" },
    { value: "NOKIA 8800", label: "NOKIA 8800" },
    { value: "APPLE IPHONE", label: "APPLE IPHONE" },

  ];

export class AdminAddProduct extends Component {
    constructor(props) {
        super(props);
        this.state={
            fckShort:'',
            fckLong:'',
            title:'',
            url:'',
            price:0,
            regular_price:0,
            cate:'',
            urlAlbum1:'',
            urlAlbum2:'',
            urlAlbum3:'',
            urlAlbum4:'',
            selectedOption: '',
            isUpdate:false,
            isChecked: false,
        }
    }
    toggleChange = () => {
        this.setState({
          isChecked: !this.state.isChecked,
        });
      }
    _tieudeSp = event => {
        const title= event.target.value;
        this.setState({title});
        console.log(`Tiêu đề sp:`, title);
    } 
    _giaSp = event => {
        let price = event.target.value;
        this.setState({price});
        console.log(`Tiêu đề sp:`, price);
    } 
    _giaChuaGiamSp = event => {
        let regular_price = event.target.value;
        this.setState({regular_price});
        console.log(`Tiêu đề sp:`, regular_price);
    } 
    _selectBox = selectedOption => {
        this.setState({ selectedOption });
        console.log(`Option selected:`, selectedOption);
    };
    _getContentShort(content){
        console.log('Content Bai Viet: '+content);
        this.setState({fckShort:content})
    }
    _getContentLong(content){
        console.log('Content Bai Viet: '+content);
        this.setState({fckLong:content})
    }
    _getUrlImg(url){
     //   console.log('_getUrlImg: '+ JSON.stringify(url.url));
        this.setState({url:url.url}); 
    }
    _getUrlImgAlbum1(url){
       // console.log('_getUrlImgAlbum: '+ JSON.stringify(url.url));
        this.setState({urlAlbum1:url.url}); 
        //console.log('this.state.urlAlbum: '+this.state.urlAlbum);
    }
    _getUrlImgAlbum2(url){
       // console.log('_getUrlImgAlbum: '+ JSON.stringify(url.url));
        this.setState({urlAlbum2:url.url}); 
        //console.log('this.state.urlAlbum: '+this.state.urlAlbum);
    }
    _getUrlImgAlbum3(url){
       // console.log('_getUrlImgAlbum: '+ JSON.stringify(url.url));
        this.setState({urlAlbum3:url.url}); 
        //console.log('this.state.urlAlbum: '+this.state.urlAlbum);
    }
    _getUrlImgAlbum4(url){
       // console.log('_getUrlImgAlbum: '+ JSON.stringify(url.url));
        this.setState({urlAlbum4:url.url}); 
        //console.log('this.state.urlAlbum: '+this.state.urlAlbum);
    }
    _showSpLon(sp){
        if(sp){
            return (
                <div style={{paddingTop:10}}>
                    <img style={{width:"40%"}} alt="anh lon" src={sp}/>
                </div>
                
            )
        }
    }
   
    componentDidUpdate(){
        // console.log('this.state.urlAlbum: '+this.state.urlAlbum1);
        // console.log('this.state.urlAlbum: '+this.state.urlAlbum2);
        // console.log('this.state.urlAlbum: '+this.state.urlAlbum3);
        // console.log('this.state.urlAlbum: '+this.state.urlAlbum4);
        // if(this.state.isUpdate===true){
        //     this.setState({isUpdate:false});
        //     alert("Cập nhật thành công");
            
        // }
        console.log('this.state.isChecked: '+this.state.isChecked);
    }
    getData(){
       let dayNow = JSON.stringify(new Date());
       let id = Number(new Date()); 
       let title =this.state.title;
  //     console.log('title: '+title);       
       let short_description=this.state.fckShort;
    //   console.log('short_description: '+short_description);       
       let content=this.state.fckLong;
      // console.log('content: '+content);       
    //    let status=this.state.status;
      // console.log('status: '+status);       
       let cateProduct=[];
       cateProduct[0]=this.state.selectedOption.value;
       cateProduct[1]="";
       if(this.state.isChecked===true){
         cateProduct[1]="SẢN PHẨM BÁN CHẠY";
       }
       if(this.state.selectedOption.value==="SẢN PHẨM BÁN CHẠY"){
        cateProduct[1]=this.state.selectedOption.value;
       }
   //    console.log('cateProduct: '+cateProduct);       
       let img=this.state.url;
     //  console.log('img: '+img);
       let price=this.state.price;
    //   console.log('price: '+price);
       let regular_price=this.state.regular_price;
    //   console.log('price: '+regular_price);
       var gallery_image=[];
       gallery_image[0] = this.state.urlAlbum1;
       gallery_image[1] = this.state.urlAlbum2;
       gallery_image[2] = this.state.urlAlbum3;
       gallery_image[3] = this.state.urlAlbum4;
       console.log('gallery_image: '+gallery_image);
       let attrib=[];
       let author='';
       let average_rating=0;
       let post_modified_time=dayNow;
       let sku ='';
       let slug='';
       let the_date=dayNow;
       this.dataRef = database.ref('tbl_Sp/'+id);
       this.dataRef.set({
        "attrib":attrib,  
        "author":author,
        "average_rating":average_rating,
        "cateProduct":cateProduct,
        "content":content,
        "id":id,   
        "img":img,
        "post_modified_time":post_modified_time,
        "price":price,
        "regular_price":regular_price,
        "short_description":short_description,
        "sku":sku,
        "slug":slug,
        "status":'publish',
        "the_date":the_date,
        "title":title,
        "gallery_image":gallery_image,
       });
       alert("Cập nhật thành công sản phẩm");
       this.setState({isUpdate:true});
       // cập nhật thay đổi mới
       let myInt = Number(new Date());
       this.dataRef = database.ref('upDate');
       this.dataRef.set(myInt);
      // localStorage.setItem('upDate',myInt);
    }

    render() {
        
        const { selectedOption } = this.state;
        if(this.state.isUpdate===true) return <AdminAddProduct/>
        return (
            <div>
                <div className="row">
                    <div className="col-md-4">
                    <Select
                    value={selectedOption}
                    onChange={this._selectBox}
                    options={options}
                   />
                   <label>
                        <input type="checkbox"
                        checked={this.state.isChecked}
                        onChange={this.toggleChange}
                        />
                        SẢN PHẨM BÁN CHẠY
                    </label>
                   
                    </div>
                    <div className="col-md-4">
                    <input 
                    style={{width:"100%"}}
                    type="text" 
                    placeholder="Tên sản phẩm"
                    // onChange={this.handleInput}
                    onBlur={this._tieudeSp} 
                    required />
                    </div>
                    <div className="col-md-2">
                    <input 
                    style={{width:"100%"}}
                    type="text" 
                    placeholder="Giá sản phẩm"
                    // onChange={this.handleInput}
                    onBlur={this._giaSp} 
                    required />
                    </div>
                    <div className="col-md-2">
                    <input 
                    style={{width:"100%"}}
                    type="text" 
                    placeholder="Giá chưa giảm"
                    // onChange={this.handleInput}
                    onBlur={this._giaChuaGiamSp} 
                    required />
                    </div>
                </div>
                
                <div className="row">
                    <div className="col-md-6">
                    <h2>Mô tả ngắn sản phẩm1</h2>
                    <FckEditor getContent={(content)=>this._getContentShort(content)}/>
                    </div>
                    <div className="col-md-6">
                    <h2>Mô tả Chi tiết sản phẩm</h2>
                    <FckEditor getContent={(content)=>this._getContentLong(content)}/>
                    </div>
                </div>
                <p>Ảnh Sản phẩm</p>
                <div className="row">
                    <div className="col-md-6">
                    <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=anhsp'} tk={"Cập nhật Banner"} getUrlImg={(url)=>this._getUrlImg(url)} />
                    
                    </div>
                    <div className="col-md-6">
                        {this._showSpLon(this.state.url)}
                    </div>
                </div>
                <p>Album Ảnh Sản phẩm</p>
                <div className="row">
                    <div className="col-md-3">
                    <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=anhspmini1'} tk={"Cập nhật Banner"} getUrlImg={(url)=>this._getUrlImgAlbum1(url)} />
                    {this._showSpLon(this.state.urlAlbum1)}
                    </div>   
                    <div className="col-md-3">
                    <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=anhspmini2'} tk={"Cập nhật Banner"} getUrlImg={(url)=>this._getUrlImgAlbum2(url)} />
                    {this._showSpLon(this.state.urlAlbum2)}
                    </div>   
                    <div className="col-md-3">
                    <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=anhspmini3'} tk={"Cập nhật Banner"} getUrlImg={(url)=>this._getUrlImgAlbum3(url)} />
                    {this._showSpLon(this.state.urlAlbum3)}
                    </div>   
                    <div className="col-md-3">
                    <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=anhspmini4'} tk={"Cập nhật Banner"} getUrlImg={(url)=>this._getUrlImgAlbum4(url)} />
                    {this._showSpLon(this.state.urlAlbum4)}
                    </div>   
                    <hr/>
                    <button onClick={()=>this.getData()}>Cập nhật</button>
                </div>
            </div>
        );
    }
}

export default AdminAddProduct;
