import React, { Component } from 'react';
import FileUploadForm  from "./FileUploadForm";
export class AdminTragop extends Component {
    constructor(props) {
        super(props);
        this.state={
            isUpdate:'',
            url:''
        }
    }
    
    getUrlImg(url){
       // console.log('url image: '+ JSON.stringify(url.url));
        this.setState({isUpdate:"Cập nhật thành công", url:url.url});
       // alert("Cập nhật thành công");
    }
    getShowCurrent(){
        if(this.state.url===''){
            return(
                <img alt="anh" style={{width:'50%'}} src="https://azshopweb.com/khachhang/upload/uploads/tragop.gif"/>
            )
                
                    
        }
    }
    getShow(){
        if(this.state.url!==''){
            return(
                <div>
                    <h2>{this.state.isUpdate}</h2>
                    <img alt="anh" style={{width:'50%'}} src={this.state.url}/>
                </div>
            )
        }
     }
    render() {
        return (
            <div>
                <h2>Cập nhật Banner trả góp</h2>
                <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=tragop'} tk={"Cập nhật Banner trả góp"} getUrlImg={(url)=>this.getUrlImg(url)} />
                <hr />
                {this.getShowCurrent()}
                {this.getShow()}
            </div>
            
        );
       
    }
}

export default AdminTragop;
