import React, { Component } from 'react';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import formatMoney from './Function';
import ReactHtmlParser from 'react-html-parser';
import { database } from './FireBase';
// import MenuLink from './MenuLink';

class HomeContentOne extends Component {
  getBaiVietSp(bvSp){
    this.dataRef = database.ref('tbl_Baiviet');
    this.dataRef.on('value', (snapshot) => {
        let infoBaiviet = snapshot.val();
        for (const key in infoBaiviet) {
          if(infoBaiviet[key].category===bvSp){
           // console.log('spbc: '+infoBaiviet[key].title);
            this.setState({BaivietSp:infoBaiviet[key]});
        }
        }
    })
  }
  componentWillMount() {
      this.getBaiVietSp(this.props.getBvSp);
  }
  constructor(props) {
    super(props);
    this.state={
      BaivietSp:''
    }
  }
  
  getID(event,id){
    event.preventDefault();
    console.log(id);
    var item = this.props.getHomeContentOne.filter(value => value.id ===id);
    //item.reverse();
    // if(!sessionStorage.getItem(id)){
    //   sessionStorage.setItem("idsp"+id,item);
    // } 
    this.props.isProduct(item);
    //console.log('san pham: '+ JSON.stringify(item));
   // this.props.productSearch(item);
  }
  getDatMua(event,id){
    event.preventDefault();
    console.log(id);
  //  var item = this.props.getHomeContentOne.filter(value => value.id ===id);
  //  console.log(item);
    //  this.props.isProduct(item);
    sessionStorage.setItem("idsp"+id,JSON.stringify(id));
    this.props.getIsContact(id);
    //console.log('san pham: '+ JSON.stringify(item));
   // this.props.productSearch(item);
  }
  getCate(event,id){
    event.preventDefault();
   // console.log("title: "+id);
   var item=[];
    if(id==="SẢN PHẨM BÁN CHẠY"){
       item = this.props.getHomeContentOne.filter(value =>value.cateProduct[1] ===id);
      //console.log('cateProduct:' + item[0].cateProduct[0]);
      item[0].cateProduct[0]="SẢN PHẨM BÁN CHẠY";
    } else {
       item = this.props.getHomeContentOne.filter(value => value.cateProduct[0] ===id);
    }
      //this.props.isProduct(item);
    //console.log('san pham: '+ JSON.stringify(item));
   // item.push(id);

    this.props.productSearch(item.reverse());
  }

  getItem(){
     var mang_moi = this.props.getHomeContentOne.slice(0,8);
     return  mang_moi.map((value,key)=>{
         let price = formatMoney(value.price,0,".");
         return(
            
          <li key={key} className="col-6 col-md-3">
              <div className="card-body">
              {/* <Link href="/"  onClick={(event)=>this.getID(event,value.id)}  className="itembox" target="_blank">  */}
            <Link to={{ pathname: "/productDetail", search: "?id="+value.id }} className="itembox">
            <p style={{color:'#610703',fontWeight:500}} className="word-limit text-center"><b>{value.title}</b></p>
                <img alt="anh"  className="img-center" src={value.img} />
                <p style={{color:'#610703',fontWeight:700}} className="word-limit text-center">{price} đ</p>
            </Link>  
            
                <p className="word-limit text-center">
                  {/* <button onClick={(event)=>this.getDatMua(event,value.id)} type="button" class="btn btn-danger">Đặt mua</button> */}
                  <Link to={{ pathname: "/contact", search: "?id="+value.id }} class="btn btn-danger">Đặt mua</Link>
                 </p>
              </div>
          </li>
         );
     });
  }
  
    render() {
      // if(this.state.spbc){
      //   console.log('spbc: '+ JSON.stringify(this.state.spbc));
        
      // }
      // if (this.state.isProduct===true) {
      //      return <Redirect to="/product" />;
      // }
     // console.log('san pham: '+ JSON.stringify(this.props.getHomeContentOne));
        var titleCate=[];
       // var shortTitle = strip_tags(this.props.getHomeContentOne[0].short_description);
        var shortTitle = this.props.getHomeContentOne[0].short_description;

        if(this.props.getTitle==="SẢN PHẨM BÁN CHẠY"){
           titleCate = this.props.getHomeContentOne[0].cateProduct[1];
        } else {
           titleCate = this.props.getHomeContentOne[0].cateProduct[0];
        }
        return (
            <section className="section-content padding-y-sm bg">
  <div className="container">
    <header className="section-heading heading-line">
      {/* <h4 style={{color:'#610703',fontWeight:500}} className="title-section bg">{this.props.getTitle}</h4> */}
      <h4 style={{color:'#610703',fontWeight:500}} className="title-section bg">{this.state.BaivietSp.title}</h4>
    </header>
    <div className="card">
      <div className="row no-gutters">
        <div className="col-md-3">
          <article href="/" className="card-banner h-100 heightSp" style={{color: 'white',backgroundImage:"url('" + this.state.BaivietSp.img +"')"}}>
            <div className="card-body zoom-wrap">
              <h5 className="title">{this.state.BaivietSp.title}</h5>
                 <p>{ReactHtmlParser(this.state.BaivietSp.content)}</p>
                 <Link className="btn btn-warning"  onClick={(event)=>this.getCate(event,titleCate)} to='/home'>Xem tất cả</Link>
                {/* <a href="/"  onClick={(event)=>this.getCate(event,titleCate)}  className="btn btn-warning">Xem tất cả</a> */}
              {/* <img alt="anh"  src="images/items/item-sm.png" height={200} className="img-bg zoom-in" /> */}
            </div> 
          </article>
        </div> {/* col.// */}
        <div className="col-md-9">
          <ul className="row no-gutters border-cols">
            {this.getItem()}
          </ul>
       </div> {/* col.// */}
      </div> {/* row.// */}
    </div> {/* card.// */}
  </div> {/* container .//  */}
</section>

        );
    }
}
const mapStateToProps = (state, ownProps) => {
  return {
    isPro:state.isProduct,
    isContact: state.isContact,
    itemProduct:state.itemProduct,
  }
}
// this.props.getItemProduct
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    getItem: (itemProduct) => {
      dispatch({type:"GET_ITEM",itemProduct})
    },
    isProduct: (item) => {
      dispatch({type:"IS_PRODUCT",item})
    },
    productSearch: (item) => {
      dispatch({type:"PRODUCT_SEARCH",item})
    },
    getIsContact: (item) => {
      dispatch({type:"IS_CONTACT",item})
    },
    
  }
}
// this.props.addData()

export default connect(mapStateToProps,mapDispatchToProps)(HomeContentOne);
