import React, { Component } from 'react';
import FileUploadForm  from "./FileUploadForm";
export class AdminBannerSP extends Component {
    constructor(props) {
        super(props);
        this.state={
            isUpdate:'',
            url:'',
            url1:'',
            url2:'',
            url3:'',
            url4:'',
            url5:'',
            url6:'',
        } 
    }
    getUrlImg1(url){
        // console.log('url image: '+ JSON.stringify(url.url));
         this.setState({url1:url.url});
        // alert("Cập nhật thành công");
     }
    getUrlImg2(url){
        // console.log('url image: '+ JSON.stringify(url.url));
         this.setState({url2:url.url});
        // alert("Cập nhật thành công");
     }
    getUrlImg3(url){
        // console.log('url image: '+ JSON.stringify(url.url));
         this.setState({url3:url.url});
        // alert("Cập nhật thành công");
     }
    getUrlImg4(url){
        // console.log('url image: '+ JSON.stringify(url.url));
         this.setState({url4:url.url});
        // alert("Cập nhật thành công");
     }
    getUrlImg5(url){
        // console.log('url image: '+ JSON.stringify(url.url));
         this.setState({url5:url.url});
        // alert("Cập nhật thành công");
     }
    getUrlImg6(url){
        // console.log('url image: '+ JSON.stringify(url.url));
         this.setState({url6:url.url});
        // alert("Cập nhật thành công");
     }
     getShowCurrent1(){
         if(this.state.url===''){
            var myInt = Number(new Date());
            var imgSrc = "https://azshopweb.com/khachhang/upload/uploads/SPBANCHAY.png?"+myInt;
             return(
                <img alt="anh" style={{width:'20%'}} src={imgSrc}/>
             )
        }
     }
     getShowCurrent2(){
         if(this.state.url===''){
             return(
                <img alt="anh" style={{width:'20%'}} src="https://azshopweb.com/khachhang/upload/uploads/APPLEIPHONE.png"/>
             )
         }
     }
     getShowCurrent3(){
         if(this.state.url===''){
             return(
                <img alt="anh" style={{width:'20%'}} src="https://azshopweb.com/khachhang/upload/uploads/APPLEIWATCH.png"/>
             )
         }
     }
     getShowCurrent4(){
         if(this.state.url===''){
             return(
                <img alt="anh" style={{width:'20%'}} src="https://azshopweb.com/khachhang/upload/uploads/NOKIA8800.png"/>
             )
         }
     }
     getShowCurrent5(){
         if(this.state.url===''){
             return(
                <img alt="anh" style={{width:'20%'}} src="https://azshopweb.com/khachhang/upload/uploads/AIRPOD2.png"/>
             )
         }
     }
     getShowCurrent6(){
         if(this.state.url===''){
             return(
                <img alt="anh" style={{width:'20%'}} src="https://azshopweb.com/khachhang/upload/uploads/MACBOOK.png"/>
             )
         }
     }
     getShow(sp){
        if(sp){
            var myInt = Number(new Date());
            var imgSrc = sp+"?"+myInt;
            console.log('sp:'+sp);
            
            return (
                <div style={{paddingTop:10}}>
                    <p>Banner cập nhật mới</p> 
                    <img style={{width:"50%"}} alt="anh lon" src={imgSrc}/>
                </div>
                
            )
        }
     }
    render() {
        return (
            <div>
                <h2>Cập nhật Banner Sản Phẩm</h2>
                <div className="row">
                <div className="col-2">
                <p>Sản phẩm bán chạy</p>    
                <hr/>
                <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=SPBANCHAY'} tk={"Cập nhật Banner 1"} getUrlImg={(url)=>this.getUrlImg1(url)} />
                <hr />
                {/* {this.getShowCurrent1()} */}
                {this.getShow(this.state.url1)}
                
                </div>
                <div className="col-2">
                <p>Apple Iphone</p>
                <hr />
                <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=APPLEIPHONE'} tk={"Cập nhật Banner 2"} getUrlImg={(url)=>this.getUrlImg2(url)}  />
                <hr />
                {/* {this.getShowCurrent2()} */}
                {this.getShow(this.state.url2)}
                </div>
                <div className="col-2">
                <p>Apple Watch</p>    
                <hr />
                <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=APPLEIWATCH'} tk={"Cập nhật Banner 3"} getUrlImg={(url)=>this.getUrlImg3(url)}  />
                <hr />
                {/* {this.getShowCurrent3()} */}
                {this.getShow(this.state.url3)}
                </div>
                <div className="col-2">
                <p>Nokia 8800</p>
                <hr />
                <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=NOKIA8800'} tk={"Cập nhật Banner 4"} getUrlImg={(url)=>this.getUrlImg4(url)}  />
                <hr />
                {/* {this.getShowCurrent4()} */}
                {this.getShow(this.state.url4)}
                </div>
                <div className="col-2">
                <p>Phụ Kiện ZIN</p>
                <hr />
                <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=AIRPOD2'} tk={"Cập nhật Banner 4"} getUrlImg={(url)=>this.getUrlImg5(url)}  />
                <hr />
                {/* {this.getShowCurrent5()} */}
                {this.getShow(this.state.url5)}
                </div>
                <div className="col-2">
                <p>Laptop - PC</p>
                <hr />
                <FileUploadForm url={'https://azshopweb.com/khachhang/upload/upload.php?id=MACBOOK'} tk={"Cập nhật Banner 4"} getUrlImg={(url)=>this.getUrlImg6(url)}  />
                <hr />
                {/* {this.getShowCurrent6()} */}
                {this.getShow(this.state.url6)}
                </div>
                </div>
            </div>
        );
    }
}

export default AdminBannerSP;
